.menu {
  max-width: 900px;
  flex: 1;
  margin: 65px auto 40px;
  position: relative;
}
.slider-arrow-bottom {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  bottom: -20px;
}
.slider-arrow-bottom i {
  font-size: 20px;
  text-align: center;
  color: var(--color-main);
}
.slider-arrow {
  font-size: 55px;
  top: 33%;
  font-weight: 300;
  position: absolute;
  color: var(--color-text);
  transition: .2s ease;
  cursor: pointer;
}
.slider-arrow:hover {
  color: var(--color-main);
  transition: .2s ease;
}
.arrow-next {
  right: -60px;
}
.arrow-prev {
  left: -60px;
}
