.controls-top {
    position: absolute;
    width: 100%;
    top: 20px;
    left: 0;
    z-index: 999;
  }
  .title-wrapper {
    position: absolute;
    left: 20px;
  }
  .video-title {
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 1px;
  }
  .volume-percentage {
    font-size: 26px;
    font-weight: 500;
    position: absolute;  
    left: 48%;
  }
  .fade {
    opacity: 0;
    transition: opacity 3s ease;
  }
  