.controls-bottom {
    position: relative;
    margin-top: -50px;
    display: flex;
    align-items: center;
    height: 50px;
    background-color: rgba(0, 0, 0, 0.9);
    width: 100%;
    z-index: 9999;
  }
  .controls-hide {
    opacity: 0;
    transition: opacity 1s 2s ease; 
  }
  .controls-button {
    display: flex;
    justify-content: center;
  }
  .controls-button i {
    cursor: pointer;
    padding: 5px;
    font-size: 35px;
  }
  .play-button i {
    margin: 0 20px 0 15px;
  }
  .play-button i:hover {
    transform: scale(1.1);
    transition: 0.15s ease;
    text-shadow: 1px 1px 15px var(---color-text);
  }
  .volume {
    position: relative;
  }
  .volume i {
    font-size: 29px;
    padding: 12px 0;
    margin: 0 10px 0 30px;
  }
  .volume i:hover + .volume-control-wrapper {
    display: block;
  }
  .volume i:hover {
    transform: scale(1.1);
    transition: 0.15s ease;
    text-shadow: 1px 1px 15px var(--color-text);
  }
  .volume-control-wrapper {
    display: none;
    position: absolute;
    height: 100px;
    width: 30px;
    top: -99px;
    margin-left: 9px;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 999;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    cursor: pointer;
  }
  .volume-control-inner {
    position: absolute;
    height: 85px;
    width: 13px;
    margin-top: 9px;
    margin-left: 8.5px;
    left: 0;
    top: 0;
    background-color: var(--color-secondary);
  }
  .volume-control {
    position: absolute;
    width: 100%;
    height: 50%;
    bottom: 0;
    left: 0;
    background-color: var(--color-main);
    border-top-left-radius: 1px;
  }
  .volume-control-wrapper:hover {
    display: block;
  }
  .time {
    margin-top: -2px;
    font-size: 14px;
  }
  .start-time {
    margin-right: 7px;
    flex: stretch;
  }
  .end-time {
    margin-left: 7px;
  }
  .resize i {
    margin-right: 20px;
  }
  .resize i:hover {
    transform: scale(1.1);
    transition: 0.15s ease;
    text-shadow: 1px 1px 15px var(--color-text);
  }
  .line-wrapper {
    flex: 1;
    width: 100%;
    height: 10px;
    background-color: var(--color-secondary);
    border-radius: 2px;
    position: relative;
    cursor: pointer;
  }
  .line-inner {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: var(--color-main);
  }
  .line-inner-ease {
    transition: width .3s ease;
  }
  .hovered-time {
    position: absolute;
    top: -30px;
    padding: 5px 10px;
    font-size: 11px;
    border-top-left-radius: 1px;
    border-top-right-radius: 1px;
    background-color: rgba(0,0,0,.7);
    display: none;
  }
  .line-wrapper:hover .hovered-time {
    display: block;
  }