.player-block {
  width: 100%;
  display: flex;
  justify-content: center;
  outline: none;
}
.video-wrapper {
  width: 100%;
  max-width: 1200px;
  position: relative;
}
.video {
  width: 100%;
  height: 100%;
}
.fullscreen {
  position: absolute;
  max-width: 100%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}


