.slider-item {
  position: relative;
  min-width: 200px;
  height: 150px;
  cursor: pointer;
  margin: 0 5px;
  transition: 0.25s ease;
}
.slider-item:hover img {
  opacity: 0.7;
  transition: 0.25s ease;
}
.slider-item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
