.main-title-wrapper {
    display: flex;
    justify-content: center;
}
.main-title {
    font-size: 70px;
    font-weight: bold;
    text-align: center;
    color: var(--color-main);
    font-family: 'Parisienne';
    margin-left: 30px;
  }
  .btn-switch-theme {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      margin-right: 10px;
      background: transparent;
      border: 3px solid #fff;
      cursor: pointer;
  }
  .theme-1 {
    background-color: #ec174f;
    margin-left: 30px;
  }
  .theme-2 {
    background-color: #ff2c2c;
  }