.pause-button-big-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .pause-button-big {
    font-size: 100px;
    cursor: pointer;
    transition: .1s ease;
  }
  .hovered {
    color: var(--color-main);
  }
  